.statistics {
  border: 1px solid silver;
  padding: 20px;
  margin-top: 50px;
  margin-bottom: 50px;
  text-align: center;
  width: 300px;
  height: 150px;
  margin-left: 20px;
}

.title {
  font-weight: bold;
  line-height: 20px;
}

.stat_list {
  width: 100%;
  height: 50px;
  display: inline-flex;
  margin-top: 10px;
  padding-left: 0;
  list-style: none;
}

.item {
  display: grid;
  border: 1px solid silver;
  width: 100%;
  height: 100%;
  text-align: center;
}

.label {
  color: #fff;
  font-size: 12px;
  line-height: 12px;
  padding-top: 10px;
  margin-bottom: 3px;
}

.percentage {
  color: #fff;
  font-weight: bold;
  font-size: 15px;
  line-height: 17px;
}
