.container {
  padding: 50px;
  background: silver;
}

.profile {
  background: #fff;
  width: 230px;
  height: 270px;
  border-radius: 4px;
  box-shadow: 2px 2px 2px gray;
}

.avatar {
  padding-top: 20px;
  width: 80px;
  height: 100%;
  border-radius: 50%;
  margin-bottom: 5px;
}

.description {
  text-align: center;
}

.name {
  font-weight: bold;
  font-size: 15px;
  line-height: 15px;
}

.tag,
.location {
  font-size: 13px;
  line-height: 15px;
  color: rgb(139, 134, 134);
}

.stats {
  display: inline-flex;
  margin-top: 10px;
  list-style: none;
  background: gainsboro;
  margin-left: 0px;
  padding: 0;
  height: 52px;
  width: 100%;
}

.item {
  display: grid;
  border: 1px solid silver;
  width: 100%;
  height: 100%;
  text-align: center;
  padding-bottom: 20px;
}

.label {
  color: grey;
  font-size: 12px;
  line-height: 12px;
  padding-top: 10px;
  margin-bottom: 3px;
}
.quantity {
  font-weight: bold;
  font-size: 15px;
  line-height: 17px;
}
