td,
th {
  border: 1px solid #fff;
  height: 20px;
  color: rgb(75, 71, 71);
  text-align: center;
}

.transaction_history {
  border-collapse: collapse;
  box-shadow: 4px 4px 6px -2px rgba(0, 0, 0, 0.39);
  margin-bottom: 30px;
  width: 400px;
}

.head {
  background-color: lightblue;
}

.items {
  font-size: small;
  color: gray;
}

.items:nth-child(even) {
  background-color: rgb(228, 225, 225);
}
